
























import {TABS_ACTION, TabsActions} from "@/types/store/TabStore";
import {Component, Vue} from "vue-property-decorator";
import {Action, Getter} from "vuex-class";

@Component
export default class NavMenu extends Vue {
  @Getter("permission/status") readonly status!: number;

  @Action(TABS_ACTION.UPDATE_CURRENT_TAB)
  updateTab!: TabsActions["UPDATE_CURRENT_TAB"];

  goToTab(tab: any): void {
    this.$router.push({query: {tab: tab.name}});
    this.updateTab(tab.name);
    this.currentIndex = tab.index;
    this.defaultMenuName = tab.name;
  }

  currentIndex: number = 0;
  defaultMenuName: string = "";

  mounted(): void {
    const tab = this.status === 2 ? this.rootTabs[0] : this.userTabs[0];
    this.goToTab(tab);
  }

  get isRoot(): boolean {
    return this.status === 2;
  }

  get upperTabs(): any[] {
    return this.isRoot
      ? []
      : this.userTabs.filter((tab) => {
          return tab.permission === this.status || tab.permission === -1;
        });
  }

  get lowerTabs(): any[] {
    return this.isRoot
      ? this.rootTabs
      : this.adminTabs.filter((tab) => {
          return tab.permission === this.status || tab.permission === -1;
        });
  }

  get eventTabs(): any[] {
    return this.isRoot ? [] : this.demoTabs;
  }

  // move to vuex store when api done
  userTabs = [
    {index: 0, title: "My Labs", name: "Lab", permission: -1},
    {index: 1, title: "Sequence Manager", name: "SequenceManager", permission: 1},
    {index: 2, title: "Sequence Manager", name: "UserSequenceManager", permission: 0},
    {index: 3, title: "Sequence Store", name: "SequenceStore", permission: 1},
    {index: 4, title: "Sequence Store", name: "UserSequenceStore", permission: 0},
    // {index: 5, title: "Project Manager", name: "ProjectManager", permission: -1},
    // {index: 6, title: "Project Worker", name: "ProjectWorker", permission: -1},
    // {index: 7, title: "Transducer DB Settings", name: "TransducerDbSettings", permission: -1},
    {index: 8, title: "Transducer DB", name: "TransducerDbDevice", permission: -1},
  ];

  adminTabs = [
    {index: 9, title: "User Account Management", name: "UserAccountManagement", permission: 1},
    {index: 10, title: "Plan Management", name: "PlanManagement", permission: 1},
    // {index: 11, title: "Plan Setting", name: "PlanSetting", permission: 1},
    {index: 12, title: "Device Management", name: "IoT", permission: 1},
    {index: 13, title: "OTA Server", name: "OTA", permission: 1},
  ];

  rootTabs = [{index: 14, title: "Admin Account Management", name: "AdminAccountManagement", permission: 2}];

  demoTabs = [{index: 14, title: "DemoEvent", name: "DemoEvent", permission: -1}];
}
